import React, { Component } from 'react';
import './chat.component.css';
import '../../../utilities/slimscroll/slimscroll.css';
// import '../../../utilities/slimscroll/slimscroll';
import { AppService } from '../../../services/app.service';
import Convo from '../conversation/convo.component';
import botpic from '../../../bot1.png';




export default class Chat extends Component {

    appService;
    constructor(props) {
        super(props);
        this.state = {
            data: [],
            loading: [],
            isOpen: false,
            category_name: '',
            measuring_unit: '',
            showChatArea: false
        }
        this.appService = new AppService();


    }

    render() {
        return (this.state.showChatArea) ? (
            <div className="slideInUp">
                <div className="container clearfix">

                    <div className="chat">
                        <div className="chat-header clearfix">
                            <img src={botpic} alt="avatar" height="50" width="50" className="img img-rounded" />
                            <div className="chat-about">
                                <div className="chat-with">Chatta</div>
                                <div className="chat-num-messages">
                                    {/* typing... */}
                                </div>
                            </div>
                            <span id="close-chat" onClick={this.toggleChatDisplay}>
                                <i className="fa fa-times fa-2x"></i>
                            </span>

                        </div>

                        <Convo />

                        <div className="chat-message clearfix">
                            <textarea name="message-to-send" id="message-to-send" placeholder="Type your message" rows="2"></textarea>
                            <i className="fa fa-file-o"></i> &nbsp;&nbsp;&nbsp;
<i className="fa fa-file-image-o"></i>

                        </div>
                    </div>
                </div>

            </div>
        ) :
            (<button id="chat-opener" title="Chat with us" onClick={this.toggleChatDisplay}>
                <i className="far fa-comment-alt fa-2x">

                </i>
            </button>)
    }



    componentDidMount() {

        // this.appService.getAllUsers().then((users) => {
        //     console.log('All Users ', users);
        // }).catch((error) => {
        //     console.log('Error ', error);
        // })
    }

    toggleChatDisplay = () => {
        
        this.setState({
            showChatArea: !this.state.showChatArea
        })
    }



}